import React from 'react';
import sss from './post.module.css';

const Post = (props) => {
    return (

        <div className={sss.item}>
            <div className={sss.avatarka}>
            <img src="data:image/jpeg;base64,"></img>
            
            
             {props.message}   
            {/* <div className={sss.mes}></div> */}
            </div>

                <div className={sss.like}><span>like </span>{props.likeCount}</div>

           
        </div>

      
    )
};

export default Post; 
